@import url(https://fonts.googleapis.com/css2?family=Castoro&family=Work+Sans:wght@400;600;700;800;900&display=swap);
.about-section {
    padding-top: 80px;
    /* padding-bottom: 100px; */
}
.about-section .container{
    position: relative;
    padding-bottom: 60px;
    border-bottom: 1px solid #c0c8cd;
}
.servicesSection .inner-box {
    position: relative;
    padding: 45px 25px 45px 35px;
    border-top: 4px solid transparent;
    height: 100%;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.servicesSection .inner-box:hover {
    border-color: #2c724f;
    background-color: #f2f5f6;
}
.inner-box .icon-box {
    position: relative;
    line-height: 1em;
    font-size: 46px;
    color: #2c724f;
    display: inline-block;
    transition: all 600ms ease;
    -moz-transition: all 600ms ease;
    -webkit-transition: all 600ms ease;
    -ms-transition: all 600ms ease;
    -o-transition: all 600ms ease;
}
.service-block {
    padding-left: 0;
    padding-right: 0;
}
.service-block {
    /* border-right: 1px solid #e3e3e3;
    border-bottom: 1px solid #ebebeb; */
    border: 1px solid #e3e3e3;
}
.inner-box:hover .icon-box {
    transform: scale(-1) rotate(
180deg
);
}
.inner-box h5 {
    position: relative;
    font-weight: 600;
    margin-top: 30px;
    margin-bottom: 15px;
}
.service-block .inner-box h5 a {
    position: relative;
    font-weight: 600;
    color: #010101;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.service-block .inner-box h5 a:hover {
    color: #2c724f;
}
.inner-box .text {
    position: relative;
    color: #666666;
    font-size: 16px;
    line-height: 1.8em;
    margin-bottom: 20px;
}
.inner-box .read-more {
    position: relative;
    color: #000000;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    top: 2px;
}
.inner-box .read-more i {
    position: relative;
    top: 1px;
    left: 2px;
}
.service-block .inner-box:hover .read-more {
    color: #2c724f;
}
.service-block .inner-box:hover .read-more i{
    left: 10px;
    transition-duration: 1s;
}

.gallery-block {
    position: relative;
    margin-bottom: 5px;
}
.gallery-block .inner-box {
    position: relative;
    overflow: hidden;
}
.gallery-block .inner-box .image {
    position: relative;
    margin-bottom: 0px;
}
.gallery-block .inner-box .image img {
    position: relative;
    width: 100%;
    display: block;
}
.gallery-block .inner-box:hover .image .overlay-box {
    bottom: 0px;
}
.projectGallry .inner-column {
    padding-left: 2px;
    padding-right: 2px;
}
.projectGallry .column.col-lg-6.col-md-12.col-sm-12 {
    padding-left: 3px;
}

.gallery-block .inner-box .image .overlay-box {
    position: absolute;
    left: 0px;
    bottom: -140px;
    right: 0px;
    padding: 22px 30px;
    background-color: #2c724f;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.gallery-block .inner-box .image .overlay-box .overlay-inner {
    position: relative;
}
.gallery-block .inner-box .image .overlay-box .overlay-inner h3 a {
    position: relative;
    color: #ffffff;
}
.gallery-block .inner-box .image .overlay-box .overlay-inner .designation {
    position: relative;
    color: #ffffff;
    font-size: 14px;
    margin-top: 4px;
}
.gallery-block .inner-box:hover .image .overlay-box {
    bottom: 0px;
}

.team-block .inner-box .image {
    position: relative;
    border-radius: 3px;
    overflow: hidden;
    background-color: #2c724f;
}
.team-block .inner-box .image img {
    position: relative;
    width: 100%;
    display: block;
    border-radius: 3px;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.team-block .inner-box:hover .image .social-box {
    right: 10px;
}
.team-block .inner-box .image .social-box {
    position: absolute;
    right: -200px;
    bottom: 10px;
    padding: 10px 12px;
    border-radius: 3px;
    display: inline-block;
    background-color: #ffffff;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.team-block .inner-box .image .social-box li:first-child {
    margin-right: 4px;
}
.team-block .inner-box .image .social-box li {
    position: relative;
    margin: 0px 8px;
    display: inline-block;
}
.team-block .inner-box .image .social-box li a {
    position: relative;
    color: #666666;
    font-size: 16px;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.lower-box h4 a{
    color: #000;
}
.team-block .inner-box .lower-box {
    position: relative;
    padding-top: 30px;
}
.team-block .inner-box .image .social-box li a:hover {
    color: #2c724f;
}
.team-block .inner-box:hover .image img {
    opacity: 0.7;
    transform: scale(1.05,1.05);
}
.blog-section .column {
    padding: 0px;
    float: left;
}
.news-block {
    position: relative;
}
.news-block .inner-box {
    position: relative;
    background-color: #ffffff;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block .inner-box .image-column {
    position: relative;
    padding: 0px;
    float: left;
}
.news-block .inner-box .image-column .inner-column {
    position: relative;
}
.news-block .inner-box .image-column .image {
    position: relative;
}
.news-block .inner-box .image-column .image img {
    position: relative;
    width: 100%;
    display: block;
}
.news-block .inner-box .content-column {
    position: relative;
    padding: 0px;
    float: left;
}
.news-block .inner-box .content-column .inner-column {
    position: relative;
    text-align: center;
    padding: 90px 15px 80px;
}
.news-block .inner-box .content-column .arrow-one {
    position: absolute;
    top: 50%;
    left: -20px;
    border-top: 10px solid transparent;
    border-right: 20px solid #ffffff;
    border-bottom: 10px solid transparent;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block .inner-box .content-column .title {
    position: relative;
    color: #2c724f;
    font-size: 14px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block .inner-box .content-column h4 {
    position: relative;
    font-weight: 600;
    line-height: 1.3em;
    margin-top: 18px;
    margin-bottom: 35px;
}
.news-block .inner-box .post-date {
    position: relative;
    color: #999999;
    font-size: 14px;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block .inner-box .post-date span {
    position: relative;
    color: #000000;
}
.news-block .inner-box .content-column .arrow-two {
    position: absolute;
    top: 50%;
    right: -20px;
    z-index: 1;
    border-top: 10px solid transparent;
    border-left: 20px solid #ffffff;
    border-bottom: 10px solid transparent;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block-two {
    position: relative;
    height: 100%;
}
.news-block-two .inner-box {
    position: relative;
    background-color: #ffffff;
    height: 100%;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block-two .inner-box .image {
    position: relative;
}
.news-block-two .inner-box .image .arrow {
    position: absolute;
    left: 50%;
    bottom: 0px;
    z-index: 1;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 20px solid #ffffff;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block-two .inner-box .lower-content {
    position: relative;
    text-align: center;
    padding: 99px 15px;
}
.news-block-two .inner-box .lower-content .title {
    position: relative;
    color: #2c724f;
    font-size: 14px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block-two .inner-box .lower-content h4 {
    position: relative;
    font-weight: 600;
    line-height: 1.3em;
    margin-top: 18px;
    margin-bottom: 35px;
}
.news-block-two .inner-box .lower-content h4 a {
    position: relative;
    color: #000000;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}

.news-block-two .inner-box .lower-content .post-date {
    position: relative;
    color: #999999;
    font-size: 14px;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block-two .inner-box .lower-content .post-date span {
    position: relative;
    color: #000000;
}
.news-block .inner-box .content-column h4 a {
    position: relative;
    color: #000000;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.news-block .inner-box:hover {
    background-color: #2c724f;
}
.news-block .inner-box:hover .content-column .arrow-two {
    border-left-color: #2c724f;
}
.news-block .inner-box:hover .content-column .title, .news-block .inner-box:hover .post-date, .news-block .inner-box:hover .post-date span, .news-block .inner-box:hover .content-column h4 a {
    color: #ffffff;
}
.news-block-two .inner-box:hover {
    background-color: #2c724f;
}
.news-block-two .inner-box:hover .image .arrow {
    border-bottom-color: #2c724f;
}
.news-block-two .inner-box:hover .lower-content .title, .news-block-two .inner-box:hover .post-date, .news-block-two .inner-box:hover .post-date span, .news-block-two .inner-box:hover .lower-content h4 a {
    color: #ffffff;
}
:root {
	--primary: #2c724f;
	--white: #ffffff;
  }
nav.navbar {
	background: #2c724f;
	background: var(--primary);
	padding: 15px 0;
}
a.navbar-brand.active {
    color: #fff;
    font-size: 2rem;
    font-weight: 800;
}
li.nav-item a {
	color: #ffffff;
	color: var(--white);
	font-weight: 600;
	text-transform: uppercase;
	font-size: 16px;
	
}
.navbar-toggler{
	background-color: #fff;
	border: unset;
	outline: unset;
}
span.navbar-toggler-icon {
    background: #000;
}
footer a{
    text-decoration: none;
}
footer ul{
    list-style: none;
}
.logo-widget .logo{
    margin-top: 25px;
}
.logo-widget .logo h4 a{
    color: #000;
    font-weight: 700;
}
.logo-widget .call {
    position: relative;
    color: #999999;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.8em;
    text-transform: uppercase;
}
.logo-widget .call .phone {
    position: relative;
    color: #000000;
    font-size: 24px;
    display: block;
    margin-top: 15px;
    margin-bottom: 7px;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.logo-widget .call .email {
    position: relative;
    color: #000000;
    font-size: 16px;
    display: block;
    margin-top: 0px;
    text-transform: capitalize;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}
.footer-widget {
    position: relative;
    margin-bottom: 30px;
}
.main-footer .newsletter-widget .text {
    position: relative;
    color: #666666;
    font-size: 14px;
    line-height: 1.8em;
    margin-bottom: 15px;
}
.newsletter-form {
    margin-top: 35px;
}
.newsletter-form .form-group input[type="email"] {
    padding-right: 40px;
}
.list-link li .link {
    position: relative;
    color: #666666;
    font-size: 14px;
    font-weight: 400;
    transition: all 300ms ease;
}
.footer-widget h5 {
    position: relative;
    font-weight: 600;
    color: #000000;
    line-height: 1.2em;
    margin-top: 25px;
    margin-bottom: 25px;
    text-transform: capitalize;
}
.newsletter-form .form-group input[type="text"], .newsletter-form .form-group input[type="tel"], .newsletter-form .form-group input[type="email"], .newsletter-form .form-group textarea {
    position: relative;
    display: block;
    width: 100%;
    line-height: 28px;
    height: 44px;
    font-size: 14px;
    color: #000000;
    font-weight: 400;
    padding: 10px 20px 10px 20px;
    background: #f7f7f7;
    border-radius: 3px;
    transition: all 300ms ease;
    border: 1px solid #cccccc;
}
.newsletter-form .form-group button {
    position: absolute;
    right: 0px;
    bottom: 0px;
    height: 44px;
    line-height: 44px;
    color: #fff;
    font-size: 20px;
    text-align: center;
    background: none;
    width: 40px;
    cursor: pointer;
    background: none;
    text-transform: capitalize;
    background-color: #2c724f;
    border: unset;
}
.footer-bottom .bottom-inner {
    position: relative;
    padding: 35px 0px;
    border-top: 1px solid #e1e1e1;
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
	--primary: #2c724f;
	--white: #ffffff;
  } 
body{
  font-family: 'Work Sans', sans-serif;
}
h1,h2,h3,h4,h5,h6,.h1,.h2,.h3,.h4,.h5,.h6{
  font-family: 'Castoro', serif;
}
.carousel-inner img {
  width: 100%;
  
}
.carousel-item{
  position: relative;
}
.carousel-caption {
  width: 50%;
  position: absolute;
  bottom: 20%;
  background-color: #000000d4;
  border-radius: 50px  0px 50px 0px;
  padding: 2rem;
}
.heading {
  font-weight: 300;
  letter-spacing: 3px;
  padding: 10px 0;
}
.heading  span{
  color: #2c724f;
  color: var(--primary);
  font-weight: 800;
  font-size: 3rem;
}
.title {
  position: relative;
  color: #666666;
  font-size: 14px;
  line-height: 1.6em;
  letter-spacing: 1px;
  text-transform: uppercase;
}
a{
  text-decoration: none;
}
a:hover{
  color: unset;
}
.primaryBtn {
  position: relative;
    display: inline-block;
    font-size: 16px;
    line-height: 1;
    color: #ffffff;
    padding: 20px 30px;
    font-weight: 600;
    overflow: hidden;
    border-radius: 2px;
    background-color: #2c724f;
    background-color: var(--primary);
    text-transform: uppercase;
}
.primaryBtn:hover{
  background-color: #262626;
  transition-duration: 1s;
}
.text-justify{
  text-align: justify;
}
.servicesSection{
  margin-top: 80px;
}
.projectGallry{
  margin-top: 80px;
}
.team{
  margin-top: 80px;
}
.blog-section{
  margin-top: 80px;
}
.main-footer{
  margin-top: 80px;

}
/* contact us */
.contact-form {
  padding: 5rem;
  box-shadow: 3px 3px 3px 3px #e4e0e0;
}
.form-group {
  margin-bottom: 20px;
}
.form-control {
  padding: 12px 10px;
  border-radius: unset;
  border-color: #2c724f;
  outline: unset;
}
.form-control:focus{
  outline: unset;
  box-shadow: unset;
  border-color: #2c724f;
}
.companyContact {
  padding: 12rem 5rem;
  background-color: #2c724f;
  color: #fff;
}
